import './MapContainer.scss';
import React, { PureComponent } from 'react';
import PropTypes from 'prop-types';
import Map from 'pigeon-maps';
import Marker from 'pigeon-marker';
import { isNotEmptyArray } from '../../utils/utils_common';
// import { findArrayItemIndex } from '../../utils/utils_common';

class MapContainer extends PureComponent {
  static propTypes = {
    markersList: PropTypes.arrayOf(PropTypes.object),
    currentLatLng: PropTypes.arrayOf(PropTypes.number),
    cbSetMarkersList: PropTypes.func,
    cbSetLatLng: PropTypes.func,
  };

  static defaultProps = {
    markersList: [],
    currentLatLng: [],
    cbSetMarkersList: null,
    cbSetLatLng: null,
  };

  constructor(props) {
    super(props);
    this.state = {
      mapHeight: 800,
      mapWidth: 800,
    };
  }

  componentDidMount() {
    this.fitMapSize();
    this.prepareListeners();
  }

  componentWillUnmount() {
    this.removeListeners();
  }

  prepareListeners = () => {
    window.addEventListener('resize', this.handleWindowResize, false);
  };

  removeListeners = () => {
    window.removeEventListener('resize', this.handleWindowResize);
  };

  fitMapSize = () => {
    const { width: mapWidth, height: mapHeight } = this.mapContainer.getBoundingClientRect();
    this.setState({ mapWidth, mapHeight });
  };

  handleWindowResize = e => {
    this.fitMapSize();
  };

  handleMapClick = ({ event, latLng }) => {
    this.setLatLng(latLng);
    // this.addMarkerToList(latLng);
  };

  handleMarkerClick = ({ event, anchor, payload }) => {
    // console.log('marker: ', payload);
  };

  setLatLng = latLng => {
    const { cbSetLatLng } = this.props;
    !!cbSetLatLng && cbSetLatLng(latLng);
  };

  getMarkers = () => {
    const { markersList, currentLatLng } = this.props;
    let list = isNotEmptyArray(currentLatLng)
      ? [ { id: -1, latLng: currentLatLng, selected: true } ]
      : [];
    return list.concat(markersList)
      .filter(marker => marker.selected)
      .map(marker => {
      const { id, latLng } = marker;
      return (
        <Marker
          key={ id }
          anchor={ latLng }
          payload={ id }
          onClick={ this.handleMarkerClick }
        />
      );
    });
  };

  render() {
    const { mapWidth, mapHeight } = this.state;
    const markers = this.getMarkers();

    return (
      <div
        className='MapContainer'
        ref={ elm => { this.mapContainer = elm; } }
      >
        <Map
          center={ [53.9, 27.55] }
          zoom={ 12 }
          width={ mapWidth }
          height={ mapHeight }
          onClick={ this.handleMapClick }
        >
          { markers }
        </Map>
      </div>
    )
  }
}

export default MapContainer;
